import React from 'react';
import { useDispatch } from 'react-redux';
import { useLocation } from 'react-router-dom';
import { startEliminar } from '../../actions/auth';
import {isMobile} from 'react-device-detect';

export const RppItemsDetalle = ({rpps,loading}) => {
  const dispatch = useDispatch();
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  if(loading){
    return <h2>Cargando...</h2>
  }
  const handleGoDelete = (uid) => {
      dispatch(startEliminar(uid,idEvento));
      const timer = setTimeout(() => {
        window.location.reload();
      }, 500);
      return () => clearTimeout(timer);
  };
  return(
  <>
    {rpps && <table className={isMobile ? "d-block overflow-scroll table table-sm table-bordered table-striped w-auto":"table table-sm table-bordered w-auto table-striped"}>
  <thead className="bg-dark text-light">
    <tr>
      <th className="text-center" scope="col">DNI</th>
      <th className="text-center" scope="col">Nombre</th>
      <th className="text-center" scope="col">Deuda</th>
      {/* <th className="text-center" scope="col">Total Entradas</th>
      <th className="text-center" scope="col">Total Favor</th> */}
      {/* <th className="text-center" scope="col">Desasociar</th> */}
    </tr>
  </thead>
  <tbody>
  {rpps.map(item => {
      return (
        <tr key={item.uid}>
          <td className="text-center">{ item.dni }</td>
          <td className="text-center">{ item.nombre }-{ item.apellido }</td>
          {item.eventos.map(evento => {
                return evento.evento === idEvento &&( <td key={item.uid} className="text-center">{ evento.deuda }</td>)
            })}
          {/* {item.eventos.map(evento => {
              return  <td key={item.uid} className="text-center">{ evento.totalEntradas?evento.totalEntradas:"-" }</td>
          })}
          {item.eventos.map(evento => {
              return  <td key={item.uid} className="text-center">{ evento.totalEntradas-evento.deuda }</td>
          })} */}
          {/* <td className="text-center">{ item.deuda?item.deuda:"-"}</td> */}
          {/* <td className="text-center"><button className='btn btn-danger' onClick={()=>handleGoDelete(item.uid)}><i className="fas fa-trash-alt"></i></button></td> */}
        </tr>
      );
    })}
  </tbody>
</table>}
</> );
};
