import React from 'react';
import { Page, Text, View, Document, Image} from '@react-pdf/renderer';

export const Comprobante = ({datos}) => {
    return (
      <Document>
        <Page
          size="A4"
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "start",
            alignItems: "center",
            backgroundColor: "white",
          }}
        >
          <View
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "start",
              alignItems: "center",
              backgroundColor: "white",
              padding: 5,
            }}
          > 
            <Text style={{ marginTop:"35px",fontSize: "45px" }}>Evento</Text>
            <Text style={{ color: "#08298A",fontSize: "50px",marginBottom:"100px" }}>{datos.evento ? datos.evento : ""}</Text>
            {datos.uri&& <Image allowDangerousPaths src={datos.uri} style={{ width:"256px", height: "256px",marginTop:"15px" }}></Image>}
          </View>
        </Page>
      </Document>
    );
};
